  max-width: 920px;
  margin: 22px auto 0;
}
.playlist__top-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.playlist__title {
  color: #fff;
  font-size: 24px;
  font-weight: 500;
}
.playlist__video {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 30px;
}
.playlist__video_text {
  font-size: 16px;
  font-weight: 500;
}
.playlist__video_date {
  color: #bbb;
  font-size: 16px;
  font-weight: 300;
}
.playlist__descr {
  color: #eee;
  font-size: 14px;
  font-weight: 400;
  margin-top: 33px;
}
.playlist__selected-videos {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  background: #2e2b2b;
  position: absolute;
  width: 100%;
  left: 0;
  padding: 24px;
  margin-top: 32px;
