  border-radius: 8px;
  border: 1px solid #ad7955;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
}
.dotBtn svg {
  transition: 0.3s;
}
.dotBtn__wr {
  position: relative;
}
.dotBtn__menu {
  background: #222;
  padding: 0 10px;
  width: 210px;
  position: absolute;
  right: 0;
  bottom: -65px;
  display: none;
}
.dotBtn__menu.active {
  display: block;
}
.dotBtn__li {
  color: #bbb;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
  margin: 10px 0;
}
.dotBtn__li:hover {
  color: #fff;
}
.dotBtn__li a {
  color: inherit;
}
.dotBtn__li a::visited {
  color: #bbb;
